<template>
  <div class="row">
    <div class="card" v-for="u in userInfo.list" :key="u.id">
      <a :href="u.html_url" target="_blank">
        <img :src="u.avatar_url" style='width: 100px'/>
      </a>
      <p class="card-text">{{ u.login }}</p>
    </div>
    <h1 v-show="userInfo.isFirst">welcome</h1>
    <h1 v-show="userInfo.isLoading">loading</h1>
  </div>
</template>

<script>
export default {
  name: "List",
  data() {

    return {

      userInfo:{

        isFirst:true,
        isLoading:false,
        errorMsg:"",
        list:[],
      }
    }
  },
  mounted() {

    this.$bus.$on("userInfo", (userInfo) => {

      this.userInfo = {...this.userInfo,...userInfo}

    })
  }
}
</script>

<style scoped>

.album {
  min-height: 50rem; /* Can be removed; just added for demo purposes */
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: #f7f7f7;
}

.card {
  float: left;
  width: 33.333%;
  padding: .75rem;
  margin-bottom: 2rem;
  border: 1px solid #efefef;
  text-align: center;
}

.card > img {
  margin-bottom: .75rem;
  border-radius: 100px;
}

.card-text {
  font-size: 85%;
}
</style>
